import React, { useEffect } from "react";
import { Link } from '@reach/router';

const Header = () => {
  const [openMenu, setOpenMenu] = React.useState(false);
  const closeMenu = () => {
    setOpenMenu(false);
  };
  useEffect(() => {
    const header = document.getElementById("myHeader");
    const totop = document.getElementById("scroll-to-top");
    const sticky = header.offsetTop;
    const scrollCallBack = window.addEventListener("scroll", () => {
      if (window.pageYOffset > sticky) {
        header.classList.add("sticky");
        totop.classList.add("show");
      } else {
        header.classList.remove("sticky");
        totop.classList.remove("show");
      }
      if (window.pageYOffset > sticky) {
        closeMenu();
      }
    });
    return () => {
      window.removeEventListener("scroll", scrollCallBack);
    };
  }, []);
  return (
    <header id="myHeader" className="navbar white">
      <div className="mainside">
        <div className="menuItem">
          <a className='menulink' href="#outline"><b>STORY</b></a>
          <a className='menulink' href="#unique"><b>UNIQUE</b></a>
          <a className='menulink' href="#roadmap"><b>ROADMAP</b></a>
          <a className='menulink' href="#team"><b>TEAMS</b></a>
          <a className='menulink' href="#faq"><b>FAQ</b></a>
        </div>
        <div className="menuBtnArea">
          <div className="btn-icons">
            <div>
              <a href="https://twitter.com/champions_ninja" target="_blank"><img src="../../../assets/IconTwiter.png" alt="" /></a>
            </div>
            <div>
            <a href="https://discord.gg/DEFWs2ZJFq" target="_blank"><img src="../../../assets/IconDiscord.png" alt="" /></a>
            </div>
            <div>
            <a href="https://www.instagram.com/ninjachampions" target="_blank"><img src="../../../assets/IconInstagram.png" alt="" /></a>
            </div>
            <div>
            <a href="https://opensea.io/collection/ninjachampions" target="_blank"><img src="../../../assets/IconOpensea.png" alt="" /></a>
            </div>
          </div>
          <div className="space"></div>          
          <div className="btn-main">
            <span>
              <span>Mint Soon</span>&nbsp;&nbsp;
              <img src="../../../assets/Tool_Star.png" alt="" />
            </span>
          </div>
        </div>
      </div>
    </header>
  );
};
export default Header;
